@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');


.App{
    background-image:url("https://mocah.org/uploads/posts/124416-miui-8-rainy-weather-background-minimal-hd.png");
    position:fixed;
    min-width: 100%;
    min-height: 100%;
}

body{
    margin: 0;
    padding : 0;
    font-family: poppins, serif;
    font-size:15px;
}


.search-box{
    position:absolute;
    top : 10%;
    left: 50%;
    Transform: translate(-50%,-50%);
    background : #2f3640;
    border-radius : 40px;
    padding : 20px;
    width: 700px;
}

.search-txt{
   width : 670px;
    background: none;
    outline :none;
    padding:0;
    border:none;
    color:white;
}

.date{
    color:lightgray;
    font-size:15px;
}

.temp{
    color:#fff;
    font-size:70px;

}

.search-button{
    outline:none;
    border : none;
    background: none;
    color:orange;
}


.icon{
    position: absolute;
    top: 45%;
    left:90%;

}


.current-weather{
    display: flex;
    justify-content: space-between;

}


#description{
    font-size: 19px;
}

.cards{
    border-radius:20px;
    position: absolute;
    top:27%;
    left:54%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

.child_cards {
    flex: 0.5 0 30%;
    color:black;
    text-align: center;
    margin : 10px 5px;
}


.cards h2{
    margin:10px 10px ;
    padding : 0;

}

.child-style{
    color:lightgrey;
    font-size:23px;
}

.transparent h2{
    margin:0;
    padding : 0;
}


.cityname{
    position:absolute;
    top:15%;
    left:3%;
}

.picture{
    background-size:cover;
}


.forecast{

    position:absolute;
    top : 70%;
    left:5%;
    display : flex;
    justify-content: space-between;
}

.single{
        margin : 2px 2px;
        width:140px;
        border-radius : 20px;
}

.single h6{
    text-align: center;
    margin:3px;
    font-size:18px;
}

#imgs{
    display: block;
    margin: 0 auto;
}

.transparent{
    background-color: rgba(255,255,255,0.2);
}

.error{
    position: absolute;
    top:40%;
    left : 30%;
   font-size: 23px;
    padding : 5px 5px;
    border-radius: 12px;
}